import React from 'react';
import { ShortText } from './ShortText';
import { useEntityPageContext } from '@wix/patterns';
import { useController } from '@wix/patterns/form';
import { Field, PatternsFieldType } from '../../../types';
import { Checkbox } from './Checkbox';
import { DateInput } from './DateInput';
import { DateTime } from './DateTime';
import { Number } from './Number';
import { LongText } from './LongText';
import { Url } from './Url';
import { BaseInputProps } from './types';
import { ImageInput } from './ImageInput';

export const EDITABLE_INPUT_ELEMENTS: Partial<
  Record<PatternsFieldType, React.ComponentType<any>>
> = {
  SHORT_TEXT: ShortText,
  LONG_TEXT: LongText,
  BOOLEAN: Checkbox,
  DATE: DateInput,
  DATETIME: DateTime,
  NUMBER: Number,
  URL: Url,
  IMAGE: ImageInput,
};

export interface FormFieldInputProps {
  field: Field;
  inputRef?: BaseInputProps<any>['inputRef'];
}

export const FormFieldInput = (props: FormFieldInputProps) => {
  const { field, inputRef } = props;

  const pageState = useEntityPageContext();

  const controller = useController({
    name: field?.id ?? '',
    control: pageState.form.control,
    defaultValue: (pageState.entity as any)?.[field?.id ?? ''],
  });

  if (!EDITABLE_INPUT_ELEMENTS.hasOwnProperty(field.type) || !pageState) {
    return null;
  }

  const FieldInput = EDITABLE_INPUT_ELEMENTS[field.type]!;

  return (
    <FieldInput
      field={field}
      value={controller.field.value}
      onChange={controller.field.onChange}
      dataHook={`form-field-${field.id}`}
      inputRef={inputRef}
    />
  );
};
